import { Account, Address, Currency, PayeeSummary } from '@backbase/data-ang/billpay';
import { Schedule } from './billpay-payment-schedule.model';

export interface PaymentFormState {
  paymentId?: string;
  accountFrom: Account;
  payeeTo: PayeeSummary;
  amount: PaymentAmount;
  memo?: string;
  schedule: Schedule;
  overnightDeliveryAddress?: Address;
}

export interface PaymentAmount {
  currency: string;
  amount: string;
  type?: string;
}

export type PaymentType = 'oneOff' | 'recurring';

export interface PaymentReview {
  payeeID?: string;
  payeeName: string;
  payeeAccountNumber?: string;
  payeeNickName?: string;
  amount?: Currency;
  fee?: Currency;
  paymentDate: string;
  paymentMemo?: string;
  paymentAccount?: Account;
  recurring: boolean;
  numberOfInstances?: number;
  frequency?: string;
  paymentScheduledAlert?: boolean;
  paymentSentAlert?: boolean;
  modelExpirationAlert?: boolean;
  address?: Address;
  overnightDeliveryAddress?: Address;
}
